body{
  background-color: #8f327b;
  color: rgb(241, 175, 247);
  margin: 20px;
  font: 32px "Century Gothic", Futura, sans-serif;
  /* border: 1px solid black; */
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.game{
  display: flex;
  flex-direction: column;
  align-items: center;

}

.board{
  background-color: rgb(207, 7, 191);
  border: 1px solid #770b77;
  margin:10px;
  padding: 20px;
}

.squarerow{
  display: flex;
  flex-flow: row nowrap;

}

.square{
  background-color: #b61a9c;
  border: 1px solid #920e92;
  /* margin: 0px; */
  padding: 0px;
  color: white;
  text-align: center;
  font-size: 84px;
  width: 100px;
  height: 100px;
}

.square:focus{
  outline: none;
  background-color: #790773;
}

.status{
  margin: 20px;
  font-size: 30px;

}

.restart{
  background-color: #770b77;
  color: rgb(241, 175, 247);
  padding: 10px;
  border: none;
}


